.container {
  padding: 15px;
}
.tarrifs {
  display: flex;
  margin: 0 -15px;
}

@media (max-width: 700px) {
  .tarrifs {
    flex-wrap: wrap;
  }
}

@media (max-width: 460px) {
  .tarrifs {
    margin: 0;
    flex-wrap: wrap;
  }
}

